body {
  background: #425f9c;
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
}
nav {
  display: flex;
  position: relative;
  justify-content: space-around;
  align-items: center;
  min-height: 10vh;
  background-color: rgb(98, 102, 104);
  color:white;

}

.nav-links {
  width: 70%;
  display: flex;
  justify-content: space-around;
  align-items: center;
  list-style: none;
  
}

.charts {
text-align: center;
display: flex;
justify-content: space-around;
align-items: center;

}
.mainBlock {
  border-radius: 5px;
  box-sizing: border-box;
  text-align: center;
  background-color: #fff;
  padding: 20px;
  width: 100%;
  max-width: 350px;
  margin: 100px auto;
  display: table;
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
}
.Error{
    padding: 10px;
    background-color: #feeaea;
    border: 1px solid #fadadb;
    margin-bottom: 10px;
    border-radius: 3px;
  }

  .Spinner{
    display: table;
    margin: 0 auto;
  }
  .Spinner > .ImgSpinner {
    max-width: 30px;
  }
.regField {
    width: 100%;
    background-color: #f7f7f7;
    color: #000;
    border: 1px solid #ddd;
    padding: 15px;
    box-sizing: border-box;
    font-size: 15px;
}

.submitBtn {
    width: 100%;
    background: #425f9c;
    border: 0;
    color: #fff;
    padding: 15px;
    margin: 10px 0 0 0;
    font-size: 15px;
    cursor: pointer;
}

.underLine {
    text-align: center;
    margin: 7px 0 0 0;
    display: block;
}

.linkBtn {
    border: 0;
    background-color: unset;
    text-decoration: underline;
    color: #3c4043;
    padding: 0;
    margin: 0;
}

.linkBtn:hover {
    color: blue;
    cursor: pointer;
}

.linkBtn:focus,
.submitBtn:focus{
    outline: none;
}

.trackerBlock {
    /* border-radius: 5px; */
    box-sizing: border-box;
    /* background-color: #E9E9E9; */
    padding: 20px;
    width: 100%;
    max-width: 420px;
    margin: 100px auto;
    display: table;
    /* box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19); */
}

.welcome {
    color: #fff;
    display: flex;
    justify-content: space-between;
    margin: 0;
    text-shadow: 0px 5px 3px #292929;
}
.totalMoney {
    color: #fff;
    font-size: 20px;
    margin:0 0 10px;
    text-shadow: 0px 5px 3px #292929;
}

.newTransaction {
    box-sizing: border-box;
    text-align: center;
    color: #425f9c;
    display: table;
    width: 100%;
    border-radius: 5px;
    background-color: #fff;
    border: 0;
    padding: 10px;
    margin:0 0 10px;
    font-size: 15px;
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
}

.newTransaction input[type="text"],
.newTransaction select{
    width: 100%;
    box-sizing: border-box;
    border: 1px solid #ddd;
    font-size: 13px;
    padding: 7px;
}

.newTransaction input[type="text"]:focus,
.newTransaction select:focus{
    outline: none;
}

.inputGroup {
    display:flex;
    justify-content: center;
}

.addTransaction {
    text-align: center;
    color: #ffffff;
    border-radius: 3px;
    background-color: #425f9c;
    border: 0;
    margin: 5px 0 0;
    padding: 5px 10px;
    font-size: 15px;
    cursor: pointer;
}

.addTransaction:hover {
    background-color: #274177;
}

.addTransaction:focus{
    outline: none;
}



.latestTransactions {
    margin: 10px 0 0 0;
    background-color: #fff;
    padding: 10px;
    border-radius: 10px;
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
}

.latestTransactions p{
    font-weight: bold;
    padding: 0 0 5px;
    margin: 0;
    font-size: 13px;
    border-bottom: 1px solid #ddd;
}

ul {
    list-style-type: none;
    padding: 0;
    margin: 0;
}

ul li {
    border-bottom: 1px solid #ddd;
    padding: 5px 0;
    display: flex;
    justify-content: space-between;
}

ul li:last-child{
    border-bottom: 0;
    padding: 5px 0 0;
}

.expense {
    color: red;
}

.deposit {
    color: green;
}

.exit {
    background-color: #fff;
    border: 0;
    border-radius: 3px;
    color: red;
    cursor: pointer;
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
}

.exit:hover {
    background-color: #f1f1f1;
}
